        //         vm.UserGuidData = [];
        //     } finally {
        //         vm.isLoading = false;
        //     }
        // },
        async GetSubCategoryDDL(evt, data) {
            var vm = this;
            vm.isLoading = true;
            var url = `type=${'GET_BILLTEMPLATE_CATEGORY'}`
            await DataService.GetDDLData(url).then((response) => {
                if (response.data.DATA != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = response.data.DATA;
                    (vm.FormSchema)[0].Data[3].config.options = [];
                    vm.SubCatData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[3].config.options.push({
                            name: itm.name,
                            value: itm.value
                        })
                    })

                } else {
                    vm.isLoading = false;
                }
                vm.$refs.dynamicform.UpdateKeyValue();
                vm.isLoading = false;
            });
        },
        BindFields(itm) {
            this.Template_contents += '{{' + itm + '}} ';
        },


        SubmitData() {
            debugger
            var vm = this;
            var Template_Name = vm.FormSchema[0].Data[0].value;
            var Template_Subject = vm.FormSchema[0].Data[1].value;
            var moduleId = vm.FormSchema[0].Data[2].value;
            var Template_SubCategory_Id = vm.FormSchema[0].Data[3].value;
            var Status_Id = vm.FormSchema[0].Data[4].value;
            var html = this.Template_contents;
            var objData = {
                TemplateId: (vm.EditItme.template_id > 0 ? vm.EditItme.template_id : 0),
                TemplateName: Template_Name,
                TemplateContent: html,
                StatusId: Status_Id,
                TemplateCategoryId: moduleId,
                TemplateSubCategoryId: Template_SubCategory_Id,
                TemplateSubject: Template_Subject,
                TemplateJson: "",
                TemplateType: true,
            }
            var formData = new FormData();
            formData.append("postString", JSON.stringify(objData));
            DataService.AddAdvancedBillTemplate(formData).then((response) => {
                if (response.data.status == '' && response.data.msg == "TemplateNameExists") {
                    vm.ShowAlert(vm.$t("TemplateNameExists"), "warning", vm.$t('Alert'));
                } else {
                    if (typeof response.data == 'string') {
                        let result = JSON.parse(response.data)[0];
                        if (result.status_id == 1 && result.status_msg == 'added') {
                            vm.ShowAlert(vm.$t("BillTemplatesuccessfullyadded"), "success", vm.$t('Alert'));
                            window.location.reload();
                        } else if (result.status_id == 2 && result.status_msg == 'updated') {
                            vm.ShowAlert(vm.$t("BillTemplatesuccessfullyUpdated"), "success", vm.$t('Alert'));
                            window.location.reload();
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        }
                    }
                }
            });
        },


        SetTemplateVariable(moduleId) {
            var vm = this;
            vm.isLoading = true;
            var foundObject = vm.CatData.find(x => x.module_id == moduleId);
            if (foundObject) {
                vm.UserGuidData = [];
                var template_category_name = foundObject.module_name;
                if (template_category_name == 'Appointment Calendar') {
                    vm.UserGuidData = vm.appointmentuserguide;
                }
                else if (template_category_name == 'Appointment Registration') {
                    vm.UserGuidData = vm.appointmentregistrationuserguide;
                }
                else if (template_category_name == 'Greeting') {
                    vm.UserGuidData = vm.greetinguserguide;
                }
                else if (template_category_name == 'Ticketing') {
                    vm.UserGuidData = vm.ticketinguserguide;
                }
                else if (template_category_name == 'CRM') {
                    vm.UserGuidData = [];
                }
                else if (template_category_name == 'EVENT') {
                    vm.UserGuidData = [];
                }
                else if (template_category_name == 'FSM') {
                    vm.UserGuidData = [];
                }
                else {
                    vm.UserGuidData = vm.greetinguserguide;
                }
            }
            vm.isLoading = false;
        },
